export default {
  head() {
    return {
      title: 'Add Area'
    }
  },
  data() {
    return {
      name: '',
      sequence: '',
    };
  },
  methods: {
    goBack: function () {
      this.$router.push('/04');
    },
    addArea: async function () {
      await this.$store.dispatch('area/addArea', {
        name: this.name,
        sequence: this.sequence
      });

      this.goBack();
    },
  }
}
